                        <el-autocomplete
                        class="inline-input"
                        v-model="selectCountry"
                        :fetch-suggestions="querySearch2"
                        placeholder="请输入内容"
                        style="margin: 0 0vw 0 6.2vw;width:11.2vw"
                        ></el-autocomplete>
                </el-col>
                 -->
          <el-select v-model="selectCountry" placeholder="请选择">
