              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div style="display: inline-block; widht: 25%; margin-left: 6vw">
          <span style="position: absolute; left: 51vw">选择站点:</span>
          <!-- <el-col :span="35">
                        <el-autocomplete
                        class="inline-input"
                        v-model="selectSite"
                        :fetch-suggestions="querySearch1"
                        placeholder="请输入内容"
                        style="margin: 0 0vw 0 6.2vw;width:11.2vw"
                        ></el-autocomplete>
                </el-col> -->

          <el-select
            v-model="selectSite"
            placeholder="请选择"
            style="margin-left: -1vw"
          >
            <el-option
              v-for="item in selectSiteList"
