              <th>国家</th>
              <th>sku</th>
              <th>品名</th>
              <th>问题溯源</th>
              <th>问题类型</th>
              <th>问题单量</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in containerTableData" :key="index">
              <td>{{ index + 1 + (page2 - 1) * 50 }}</td>
              <td>{{ item.platform }}</td>
              <td>{{ item.site }}</td>
              <td>{{ item.country }}</td>
              <td>{{ item.sku }}</td>
              <td>{{ item.product_name }}</td>
              <td>{{ item.problem_reason }}</td>
              <td>{{ item.problem_type }}</td>
