              <th>平台</th>
              <th>站点</th>
              <th>国家</th>
              <th>运营人员</th>
              <th>问题溯源</th>
              <th>问题类型</th>
              <th>问题单量</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in operationTableData" :key="index">
              <td>{{ index + 1 + (page5 - 1) * 50 }}</td>
              <td>{{ item.platform }}</td>
              <td>{{ item.site }}</td>
              <td>{{ item.country }}</td>
              <td>{{ item.upload_people }}</td>
              <td>{{ item.problem_reason }}</td>
              <td>{{ item.problem_type }}</td>
              <td>{{ item.count_order }}</td>
            </tr>
          </tbody>
        </table>
        <el-pagination
          background
          @current-change="handleCurrentChange5"
          :current-page="page5"
          layout="prev, pager, next, jumper"
          :total="total5"
          class="pagetotle"
          style="margin: 1vh 0 0 32vw"
        >
        </el-pagination>
      </div>
    </footer>
