</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      selectSite: "", //选择站点
      selectCountry: "", //选择国家
      selectSku: "", //选择商品SKU
      selectFactory: "", //选择工厂
      selectProblemCause: "", //选择问题溯源
      selectProblemType: "", //选择问题类型
      selectOversea: "", //选择海外仓
      selectOperationalPeople: "", //选择运营人员
      beginDate: "", //选择开始时间
      overDate: "", //选择结束时间

      // restaurants1: [],//选择站点下拉框
      // restaurants2: [],//选择国家下拉框
      restaurants3: [], //选择商品SKU下拉框
      restaurants4: [], //选择工厂下拉框
      restaurants5: [], //选择问题溯源下拉框
      restaurants6: [], //选择问题类型下拉框
      restaurants7: [], //选择海外仓下拉框
      restaurants8: [], //选择运营人员下拉框

      allTableData: [], //汇总表数据
      containerTableData: [], //按货号表数据
      factoryTableData: [], //按工厂表数据
      overseaTableData: [], //按海外仓表数据
      operationTableData: [], //按运营人员表数据

      page1: 1, //汇总表页数
      total1: 0, //汇总表总页数
      page2: 1, //货号表页数
      total2: 0, //货号表总页数
      page3: 1, //工厂表页数
      total3: 0, //工厂表总页数
      page4: 1, //海外仓表页数
      total4: 0, //海外仓表总页数
      page5: 1, //运营人员表页数
      total5: 0, //运营人员表总页数

      hidFlag: "汇总",

      selectCountryList: [], //国家下拉框数据
      selectSiteList: [] //站点下拉框数据
    };
  },
  methods: {
    // querySearch1(queryString, cb) {//选择站点
    //     var restaurants1 = this.restaurants1;
    //     var results = queryString
    //         ? restaurants1.filter(this.createFilter(queryString))
    //         : restaurants1;
    //     // 调用 callback 返回建议列表的数据
    //     cb(results);
    //     },
    //     createFilter(queryString) {
    //     return (restaurant1) => {
    //         return (
    //         restaurant1.value
    //             .toLowerCase()
    //             .indexOf(queryString.toLowerCase()) === 0
    //         );
    //     };
    // },

    // querySearch2(queryString, cb) {//选择国家
    //     var restaurants2 = this.restaurants2;
    //     var results = queryString
    //         ? restaurants2.filter(this.createFilter(queryString))
    //         : restaurants2;
    //     // 调用 callback 返回建议列表的数据
    //     cb(results);
    //     },
    //     createFilter(queryString) {
    //     return (restaurant2) => {
    //         return (
    //         restaurant2.value
    //             .toLowerCase()
    //             .indexOf(queryString.toLowerCase()) === 0
    //         );
    //     };
    // },

    querySearch3(queryString, cb) {
      //选择SKU
      var restaurants3 = this.restaurants3;
      var results = queryString
        ? restaurants3.filter(this.createFilter(queryString))
        : restaurants3;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant3 => {
        return (
          restaurant3.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch4(queryString, cb) {
      //选择工厂
      var restaurants4 = this.restaurants4;
      var results = queryString
        ? restaurants4.filter(this.createFilter(queryString))
        : restaurants4;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant4 => {
        return (
          restaurant4.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch5(queryString, cb) {
      //问题溯源
      var restaurants5 = this.restaurants5;
      var results = queryString
        ? restaurants5.filter(this.createFilter(queryString))
        : restaurants5;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant5 => {
        return (
          restaurant5.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch6(queryString, cb) {
      //问题类型
      var restaurants6 = this.restaurants6;
      var results = queryString
        ? restaurants6.filter(this.createFilter(queryString))
        : restaurants6;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant6 => {
        return (
          restaurant6.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch7(queryString, cb) {
      //海外仓
      var restaurants7 = this.restaurants7;
      var results = queryString
        ? restaurants7.filter(this.createFilter(queryString))
        : restaurants7;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant7 => {
        return (
          restaurant7.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch8(queryString, cb) {
      //运营人员下拉框
      var restaurants8 = this.restaurants8;
      var results = queryString
        ? restaurants8.filter(this.createFilter(queryString))
        : restaurants8;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant8 => {
        return (
          restaurant8.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    handleCurrentChange1(newPage) {
      // 页码切换会触发此函数
      this.page1 = newPage;
      this.$nextTick(() => {
        $(".allTable>tbody").scrollTop(0);
      });
      this.getDefaultData();
    },

    handleCurrentChange2(newPage) {
      // 页码切换会触发此函数
      this.page2 = newPage;
      if (this.selectSite == "") {
        this.selectSite = null;
      }
      if (this.selectCountry == "") {
        this.selectCountry = null;
      }
      if (this.selectSku == "") {
        this.selectSku = null;
      }
      if (this.selectFactory == "") {
        this.selectFactory = null;
      }
      if (this.selectProblemCause == "") {
        this.selectProblemCause = null;
      }
      if (this.selectProblemType == "") {
        this.selectProblemType = null;
      }
      if (this.selectOversea == "") {
        this.selectOversea = null;
      }
      if (this.selectOperationalPeople == "") {
        this.selectOperationalPeople = null;
      }
      if (this.beginDate == "") {
        this.beginDate = null;
      }
      if (this.overDate == "") {
        this.overDate = null;
      }
      axios({
        url: this._url2 + "server/customer_management/email_analysis_all/",
        method: "get",
        params: {
          page: this.page2,
          sky_type_se: "ok",
          site: this.selectSite,
          country: this.selectCountry,
          sku: this.selectSku,
          factory: this.selectFactory,
          problem_reason: this.selectProblemCause,
          problem_type: this.selectProblemType,
          warehouse_code: this.selectOversea,
          upload_people: this.selectOperationalPeople,
          begin_time: this.beginDate,
          over_time: this.overDate
        }
      }).then(res => {
        console.log(res);
        this.containerTableData = res.data.re_data;

        this.$nextTick(() => {
          $(".childTable>tbody").scrollTop(0);
        });
        this.total2 = res.data.count_data[0].count_data / 5;
      });

      if (this.selectSite == null) {
        this.selectSite = "";
      }
      if (this.selectCountry == null) {
        this.selectCountry = "";
      }
      if (this.selectSku == null) {
        this.selectSku = "";
      }
      if (this.selectFactory == null) {
        this.selectFactory = "";
      }
      if (this.selectProblemCause == null) {
        this.selectProblemCause = "";
      }
      if (this.selectProblemType == null) {
        this.selectProblemType = "";
      }
      if (this.selectOversea == null) {
        this.selectOversea = "";
      }
      if (this.selectOperationalPeople == null) {
        this.selectOperationalPeople = "";
      }
      if (this.beginDate == null) {
        this.beginDate = "";
      }
      if (this.overDate == null) {
        this.overDate = "";
      }
    },

    handleCurrentChange3(newPage) {
      // 页码切换会触发此函数
      this.page3 = newPage;
      if (this.selectSite == "") {
        this.selectSite = null;
      }
      if (this.selectCountry == "") {
        this.selectCountry = null;
      }
      if (this.selectSku == "") {
        this.selectSku = null;
      }
      if (this.selectFactory == "") {
        this.selectFactory = null;
      }
      if (this.selectProblemCause == "") {
        this.selectProblemCause = null;
      }
      if (this.selectProblemType == "") {
        this.selectProblemType = null;
      }
      if (this.selectOversea == "") {
        this.selectOversea = null;
      }
      if (this.selectOperationalPeople == "") {
        this.selectOperationalPeople = null;
      }
      if (this.beginDate == "") {
        this.beginDate = null;
      }
      if (this.overDate == "") {
        this.overDate = null;
      }
      axios({
        url: this._url2 + "server/customer_management/email_analysis_all/",
        method: "get",
        params: {
          page: this.page3,
          factory_type_se: "ok",
          site: this.selectSite,
          country: this.selectCountry,
          sku: this.selectSku,
          factory: this.selectFactory,
          problem_reason: this.selectProblemCause,
          problem_type: this.selectProblemType,
          warehouse_code: this.selectOversea,
          upload_people: this.selectOperationalPeople,
          begin_time: this.beginDate,
          over_time: this.overDate
        }
      }).then(res => {
        console.log(res);
        this.factoryTableData = res.data.re_data;
        this.$nextTick(() => {
          $(".childTable>tbody").scrollTop(0);
        });
        this.total3 = res.data.count_data[0].count_data / 5;
      });

      if (this.selectSite == null) {
        this.selectSite = "";
      }
      if (this.selectCountry == null) {
        this.selectCountry = "";
      }
      if (this.selectSku == null) {
        this.selectSku = "";
