        url: this._url2 + "server/personnel_management/get_sign_area/",
        method: "get",
        params: {
          country: this.selectCountry
        }
      }).then(res => {
        this.selectSiteList = [];
        let demo = res.data.data;
        for (let i = 0; i < demo.length; i++) {
          let obj = {
            value: demo[i],
            label: demo[i]
          };
          this.selectSiteList.push(obj);
        }
      });
    }
