table {
  /*width: 84vw;*/
  /* margin: 10px 50px; */
  border-collapse: collapse;
  margin: auto;
}

tbody {
  display: block;
  max-height: 65vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 5px !important;
}
.allTable thead,
.allTable tbody {
  margin-left: 1.8vw;
}

.allTable tr td:nth-child(1),
.allTable tr th:nth-child(1),
.allTable tr td:nth-child(3),
.allTable tr th:nth-child(3),
.allTable tr td:nth-child(4),
.allTable tr th:nth-child(4),
.allTable tr td:nth-child(8),
.allTable tr th:nth-child(8),
.allTable tr td:nth-child(12),
.allTable tr th:nth-child(12) {
  width: 4vw;
}
.allTable tr td:nth-child(2),
.allTable tr th:nth-child(2) {
  width: 12vw;
}

.allTable tr td:nth-child(7),
.allTable tr th:nth-child(7) {
  width: 18vw;
}

.allTable tr td:nth-child(11),
.allTable tr th:nth-child(11) {
  width: 10vw;
}

#containerTable tr td:nth-child(1),
#containerTable tr th:nth-child(1),
#containerTable tr td:nth-child(3),
#containerTable tr th:nth-child(3),
#containerTable tr td:nth-child(4),
#containerTable tr th:nth-child(4),
#containerTable tr td:nth-child(8),
#containerTable tr th:nth-child(8),
#containerTable tr td:nth-child(9),
#containerTable tr th:nth-child(9) {
  width: 5vw;
}

#containerTable tr td:nth-child(2),
#containerTable tr th:nth-child(2),
#containerTable tr td:nth-child(7),
#containerTable tr th:nth-child(7),
#containerTable tr td:nth-child(8),
#containerTable tr th:nth-child(8) {
  width: 8vw;
}

#containerTable tr td:nth-child(5),
#containerTable tr th:nth-child(5),
#containerTable tr td:nth-child(6),
#containerTable tr th:nth-child(6) {
  width: 14vw;
}

#factoryTable tr td:nth-child(1),
#factoryTable tr th:nth-child(1),
#factoryTable tr td:nth-child(2),
#factoryTable tr th:nth-child(2),
#factoryTable tr td:nth-child(6),
#factoryTable tr th:nth-child(6) {
  width: 5vw;
}

#factoryTable tr td:nth-child(3),
#factoryTable tr th:nth-child(3),
#factoryTable tr td:nth-child(4),
#factoryTable tr th:nth-child(4),
#factoryTable tr td:nth-child(5),
#factoryTable tr th:nth-child(5) {
  width: 16vw;
}

#overseaTable tr td:nth-child(1),
#overseaTable tr th:nth-child(1),
#overseaTable tr td:nth-child(2),
#overseaTable tr th:nth-child(2),
#overseaTable tr td:nth-child(6),
#overseaTable tr th:nth-child(6) {
  width: 5vw;
}

#overseaTable tr td:nth-child(3),
#overseaTable tr th:nth-child(3),
#overseaTable tr td:nth-child(4),
#overseaTable tr th:nth-child(4),
#overseaTable tr td:nth-child(5),
#overseaTable tr th:nth-child(5) {
  width: 16vw;
}

#operationTable tr td:nth-child(1),
#operationTable tr th:nth-child(1),
#operationTable tr td:nth-child(3),
#operationTable tr th:nth-child(3),
#operationTable tr td:nth-child(4),
#operationTable tr th:nth-child(4) {
  width: 5vw;
}

#operationTable tr td:nth-child(5),
#operationTable tr th:nth-child(5),
#operationTable tr td:nth-child(6),
#operationTable tr th:nth-child(6),
#operationTable tr td:nth-child(7),
#operationTable tr th:nth-child(7) {
  width: 10vw;
}
</style>



